.coverflow {
  /* width: 100%;
  height: 800px;
  overflow: hidden; */

  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translate(-60%);

  perspective: 1500;
  -webkit-perspective: 1500;

  backface-visibility: visible;
  transform-style: preserve-3d;
  cursor: pointer;
}

.coverflow-item {
  width: 132px;
  height: 186px;
  padding: 5px;

  border: 1px solid white;
  border-radius: 5px;
  box-shadow: 2px 2px 2px;

  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);

  background-color: #a6ffb3;

  font-size: 12px;
  color: black;
  overflow: hidden;

  transform-origin: 50% 50%;
  transition: 0.5s all cubic-bezier(0.17, 0.75, 0.58, 0.95);
}
